export class YTApiConfig{
  protected CLIENT_ID: string;
  protected DISCOVERY_DOCS: string[];
  protected SCOPE: string;

  constructor(config: initConfigs){
    this.CLIENT_ID = config.clientId;
    this.DISCOVERY_DOCS = config.discoveryDocs;
    this.SCOPE = config.scope;
  }

  getConfigs(){
    var configs = <initConfigs>{};

    configs.clientId = this.CLIENT_ID;
    configs.discoveryDocs = this.DISCOVERY_DOCS;
    configs.scope = this.SCOPE;
    return configs;
  }
}

export interface initConfigs {
  clientId: string;
  discoveryDocs: string[];
  scope: string;
}
